import React, { useEffect } from "react";
import { Segment } from "semantic-ui-react";

const PrivacyPage = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  });
  return (
    <Segment>
      <h2>TIETOSUOJAKYTNT</h2>
      <h3>
        Verkkosivun kyttjist voidaan kert ja tallentaa seuraavia tietoja:
      </h3>
      <ol>
        <li>
          IP-osoite, maantieteellinen sijainti, selaimen tyyppi ja versio,
          kyttjrjestelm.
        </li>
        <li>
          Selaustietoja, kuten mist saavuit sivustolle, vierailun kesto,
          selatut sivut.
        </li>
        <li>
          Tiedot, jotka mahdollisesti annat sivustolle kirjautuessasi, kuten
          shkpostiosoitteesi, nimesi, kyttjkuvasi, maatiedon, kielitiedon,
          aikavyhykkeen.
        </li>
        <li>Tiedot, jotka julkaiset verkkosivustolla.</li>
      </ol>
      <h3>Henkilkohtaisia tietoja voidaan kytt:</h3>
      <ol>
        <li>Verkkosivun hallintaan</li>
        <li>Sislln henkilkohtaistamiseen</li>
        <li>
          Mahdollistamaan tunnistautumista vaativien ominaisuuksien kytt
        </li>
        <li>Tilastotietojen kermiseen</li>
        <li>Estmn palvelun vrinkytt</li>
        <li>Parantamaan kyttkokemusta</li>
      </ol>
      <h3>Evsteet</h3>
      <p>
        Taukopaikat.fi ja palveluntoimittajamme kytmme evsteit
        verkkosivuillamme. Evisteit kytetn kyttjkokemuksen
        parantamiseksi kyttesssi verkkosivujamme. Niit tarvitaan, jotta
        palvelumme toimivat oikein ja parhaalla mahdollisella tavalla. Sivuilla
        kytetn seuraavien palveluntarjoajien evsteit: Google, Auth0,
        Cloudinary
      </p>
      <p>
        Sivut sisltvt linkkej kolmansien osapuolten sivuille. Taukopaikat.fi
        ei ole vastuussa niden sivujen sisllst.
      </p>
      <p>
        <b>Kyttmll sivustoa hyvksyt tietosuojakytnnn.</b>
      </p>
    </Segment>
  );
};

export default PrivacyPage;
